    margin-bottom: 20px;
}

.member {
    height: 760px;
    width: 50%;
    line-height: left;
    letter-spacing: 2px;
    font-weight: bolder;
}

.jihyun {
    background-color: rgb(208, 208, 218);
    font-size: xx-large;
    color: rgb(0, 0, 0);
    border: 5px dotted burlywood;
    border-radius: 15px;
    float: left;
}

.eunjae {
    background-color: rgb(131, 122, 122);
    font-size: xx-large;
    color: rgb(0, 0, 0);
    float: left;
    border: 5px dotted burlywood;
    border-radius: 15px;
}

.project {
    height: 775px;
    width: 50%;
    display: inline-block;
    line-height: left;
}

.coin {
    background-color: olive;
    font-size: large;
    color: white;
}

.bot {
    background-color: rgb(128, 48, 48);
    font-size: large;
    color: white;
}

.person_image {
    display: flex;
    width: 600px;
    height: 620px;
    display: block;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
